import { Component, OnInit, OnDestroy } from "@angular/core";

import { W4 } from "@app/shared/models/w4-module.model";
import { W4Service } from "./w4.service";
import { AuthService } from "@app/core/service/auth.service";
import { EmployeeService } from "../employeeinfo/employeeinfo.service";
import { SharedService } from "@app/shared/shared.service";
import { HttpResponse } from "@angular/common/http";

@Component({
  selector: "app-w4",
  templateUrl: "./w4.component.html",
  styleUrls: ["./w4.component.css"],
})
export class W4Component implements OnInit, OnDestroy {
  userId: any;
  userName: string;
  w4: W4;
  loading: boolean = false;
  errLabel: string;
  isRecidence: boolean = true;
  showUpload: boolean = false;
  blob: Blob;
  hasDownload: boolean = false;
  calculation_method: any = [
    { label: "Prior to 2020 W-4", value: 1 },
    { label: "Current W4", value: 2 },
  ];
  code_filling_status: any = [
    { label: "M", value: "M" },
    { label: "S", value: "S" },
    { label: "NRA", value: "NRA" },
    { label: "NRM", value: "NRM" },
    { label: "H", value: "H" },
  ];
  multiple_jobs: any = [
    { label: "No", value: "0" },
    { label: "Yes", value: "1" },
  ];

  constructor(
    private w4Services: W4Service,
    private authService: AuthService,
    private employeeService: EmployeeService,
    private sharedService: SharedService
  ) {}

  ngOnInit(): void {
    this.w4 = new W4();
    this.userId = this.w4Services.getUserToEidt();
    if (this.userId == null) {
      this.userId = this.authService.currentUser.employeeDetailsId;
      this.userName =
        this.authService.currentUser.fistName +
        " " +
        this.authService.currentUser.lastName;
    } else {
      this.getUser(this.userId);
    }
    this.getW4(this.userId);
    this.getUser(this.userId);
    this.checkDownload(this.userId);
  }
  getUser(userId) {
    this.employeeService.getEmployee(userId).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode == 200) {
          this.userName =
            res.responsePayload.firstName + " " + res.responsePayload.lastName;
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  getW4(userId) {
    this.w4Services.getW4Details(userId).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode == 200) {
          this.w4 = res.responsePayload as W4;
          if (res.responsePayload.nonRecidentAlien === "0") {
            this.isRecidence = false;
          } else {
            this.isRecidence = true;
          }
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  checkDownload(userId) {
    this.w4Services.downloadW4Doc(userId).subscribe((res) => {
      if (res.body.size) {
        this.hasDownload = true;
      }
    });
  }
  save() {
    this.w4.employeeDetailsId = this.userId;
    if (this.isRecidence) {
      this.w4.nonRecidentAlien = "1";
    } else {
      this.w4.nonRecidentAlien = "0";
    }
    // this.w4Services.addW4(this.w4).subscribe(
    //   (res) => {
    //     if (res.statusCode == 200) {
    //       this.sharedService.add({
    //         severity: "success",
    //         summary: "Success",
    //         detail: res.message,
    //       });
    //     } else {
    //       this.errLabel = res.message;
    //     }
    //   },
    //   (error) => {
    //     this.errLabel = error;
    //   }
    // );
    if (this.w4.w4DetailsId != null || this.w4.w4DetailsId != undefined) {
      this.w4Services.updateW4(this.w4).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
          } else {
            this.errLabel = res.message;
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    } else {
      this.w4Services.addW4(this.w4).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
          } else {
            this.errLabel = res.message;
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    }
  }

  onDeptAddCloseClick(event) {
    this.showUpload = false;
    this.checkDownload(this.userId);
  }
  showDialogToUpload() {
    this.showUpload = true;
  }
  downloadW4Form(employeeDetailsId) {
    this.sharedService.add({
      severity: "info",
      summary: "Info!",
      detail: "File is preparing to download please wait",
    });
    this.w4Services
      .downloadW4Doc(employeeDetailsId)
      .subscribe((resp: HttpResponse<Blob>) => {
        const blob = new Blob([resp.body], { type: resp.body.type });
        const url = window.URL.createObjectURL(blob);
        var link = document.createElement("a");
        link.href = url;
        let imageType = resp.body.type.substring(
          resp.body.type.lastIndexOf("/") + 1
        );
        link.download = `w4Document_${employeeDetailsId}.${imageType}`;
        link.click();
        link.remove();
        this.sharedService.add({
          severity: "success",
          summary: "Success",
          detail: "File downloaded",
        });
      });
  }
  ngOnDestroy() {
    this.w4Services.setUserToEdit(null);
  }
  onSelect() {
    console.log("onselect");
  }
}
