import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AdminDashboardComponent } from "src/app/admin/admin-dashboard/admin-dashboard.component";
import { EmployeeinfoComponent } from "src/app/admin/employeeinfo/employeeinfo.component";
import { DepartmentsComponent } from "./departments/departments.component";
import { DesignationsComponent } from "./designations/designations.component";
import { MyprofileComponent } from "@app/core/myprofile/myprofile.component";
import { CompanyComponent } from "./company/company.component";
import { ProjectComponent } from "./project/project.component";
import { DocumentCategory } from "@app/shared/models/document-category.model";
import { CategoryComponent } from "./departments-category/category.component";
import { W4Component } from "./w4/w4.component";
import { I9Component } from "./i9/i9.component";
import { PaystubsComponent } from "./paystubs/paystubs.component";

const routes: Routes = [
  { path: "", redirectTo: "dashboard", pathMatch: "full" },
  { path: "dashboard", component: AdminDashboardComponent },
  { path: "employees", component: EmployeeinfoComponent },
  { path: "departments", component: DepartmentsComponent },
  { path: "designations", component: DesignationsComponent },
  {
    path: "myprofile",
    component: MyprofileComponent,
  },
  {
    path: "employee/:id/profile",
    component: MyprofileComponent,
  },
  {
    path: "company",
    component: CompanyComponent,
  },
  {
    path: "projects",
    component: ProjectComponent,
  },
  {
    path: "category",
    component: CategoryComponent,
  },
  { path: "w4", component: W4Component },
  { path: "i9", component: I9Component },
  { path: "paystubs", component: PaystubsComponent },
];

@NgModule({
  imports: [RouterModule, RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
