import { Component, OnInit } from "@angular/core";

import { ConfirmationService } from "src/app/components/api/confirmationservice";
import { ProjectService } from "./project.service";
import { Department } from "@app/shared/models/department.model";
import { SharedService } from "@app/shared/shared.service";
import { Company } from "@app/shared/models/company-module.model";
import { Project } from "@app/shared/models/project.model";
import { CompanyService } from "../company/company.service";
import { EmployeeService } from "../employeeinfo/employeeinfo.service";
import { Employee } from "@app/shared/models/employee.model";
import { projectAssignment } from "@app/shared/models/project-assignment.model";
@Component({
  selector: "app-project",
  templateUrl: "./project.component.html",
  styleUrls: ["./project.component.css"],
})
export class ProjectComponent implements OnInit {
  cols: any[];
  displayDialog: boolean;
  clientErrorMsg: any;
  public editProjectDisplay: string = "none";
  public addProjectDisplay: string = "none";
  public viewProjectDisplay: string = "none";
  projectType: string;
  project: Project;
  lstProjects: Project[];
  lstEmployes: any[];
  loading: boolean = false;
  status: number = 1;
  // companyList: any[] = [
  //   { label: "one", value: 1 },
  //   { label: "two", value: 2 },
  //   { label: "three", value: 3 },
  // ];
  companyList: any[];
  selectedEmployee: number;
  selectedProjectAssignMent: projectAssignment;
  constructor(
    private emplService: EmployeeService,
    private companyService: CompanyService,
    private confirmationService: ConfirmationService,
    private sharedService: SharedService,
    private projectService: ProjectService
  ) {
    this.selectedProjectAssignMent = new projectAssignment();
  }

  ngOnInit(): void {
    this.cols = [
      // { field: 'departmentDetailsId', header: 'ID' },
      { field: "projectName", header: "Project Name" },
      { field: "description", header: "Description" },
      { field: "startDate", header: "Start Date" },
    ];
    this.getProjects();
    this.getCompanies();
    this.getEmployees();
  }
  getEmployees(status: number = 1) {
    this.emplService.getAllEmployees(status).subscribe(
      (res) => {
        this.loading = false;
        this.lstEmployes = [];
        if (res.statusCode == 200) {
          for (let index = 0; index < res.responsePayload.length; index++) {
            const element: Employee = res.responsePayload[index];
            this.lstEmployes.push({
              label: element.fullName,
              value: element.employeeDetailsId,
            });
          }
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  getCompanies(status: number = 1) {
    this.companyService.getAllCompanies(status).subscribe(
      (res) => {
        this.loading = false;
        this.companyList = [];
        if (res.statusCode == 200) {
          for (let index = 0; index < res.responsePayload.length; index++) {
            const element = res.responsePayload[index];
            this.companyList.push({
              label: element.companyName,
              value: element.companyDetailsId,
            });
          }
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  getProjects(status: number = 1) {
    this.loading = true;
    this.lstProjects = [];
    this.projectService.getAllProject(status).subscribe(
      (res) => {
        this.loading = false;
        this.lstProjects = [];
        if (res.statusCode == 200)
          this.lstProjects = res.responsePayload as Project[];
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  getProjectAssignMents(event) {
    this.selectedProjectAssignMent = new projectAssignment();
    this.projectService.getProjectAssignMent(event).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          let projectAssignMents = res.responsePayload as projectAssignment[];
          for (let index = 0; index < projectAssignMents.length; index++) {
            if (
              this.project.projectDetailsId ==
              projectAssignMents[index].projectDetailsId
            ) {
              this.selectedProjectAssignMent = projectAssignMents[index];
              if (this.selectedProjectAssignMent.startDate) {
                let date: string[] = this.selectedProjectAssignMent.startDate.split(
                  "-"
                );
                this.selectedProjectAssignMent.startDateFormat = new Date(
                  parseInt(date[2]),
                  parseInt(date[0]) - 1,
                  parseInt(date[1])
                );
              }
              if (this.selectedProjectAssignMent.endDate) {
                let date: string[] = this.selectedProjectAssignMent.endDate.split(
                  "-"
                );
                this.selectedProjectAssignMent.endDateFormat = new Date(
                  parseInt(date[2]),
                  parseInt(date[0]) - 1,
                  parseInt(date[1])
                );
              }
              break;
            }
          }
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }

  assignProject() {
    if (!this.selectedEmployee) {
      return;
    }
    this.selectedProjectAssignMent.projectDetailsId = this.project.projectDetailsId;
    this.selectedProjectAssignMent.employeeDetailsId = this.selectedEmployee;
    if (this.selectedProjectAssignMent.startDateFormat) {
      let day = this.selectedProjectAssignMent.startDateFormat.getDate();
      let month = this.selectedProjectAssignMent.startDateFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.selectedProjectAssignMent.startDateFormat.getFullYear();
      this.selectedProjectAssignMent.startDate = month + "-" + day + "-" + year;
    }
    if (this.selectedProjectAssignMent.endDateFormat) {
      let day = this.selectedProjectAssignMent.endDateFormat.getDate();
      let month = this.selectedProjectAssignMent.endDateFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.selectedProjectAssignMent.endDateFormat.getFullYear();
      this.selectedProjectAssignMent.endDate = month + "-" + day + "-" + year;
    } else {
      this.selectedProjectAssignMent.endDate = undefined;
    }
    if (this.selectedProjectAssignMent.projectAssignmentDetailsId) {
      this.projectService
        .updateProjectAssignMent(this.selectedProjectAssignMent)
        .subscribe(
          (res) => {
            if (res.statusCode == 200) {
              this.sharedService.add({
                severity: "success",
                summary: "Success",
                detail: res.message,
              });
              this.displayScroll();
            } else {
              this.sharedService.add({
                severity: "error",
                summary: "Error",
                detail: res.message,
              });
              this.displayScroll();
            }
          },
          (error) => {
            this.loading = false;
          }
        );
    } else {
      this.projectService
        .saveProjectAssignMent(this.selectedProjectAssignMent)
        .subscribe(
          (res) => {
            if (res.statusCode == 200) {
              this.sharedService.add({
                severity: "success",
                summary: "Success",
                detail: res.message,
              });
              this.displayScroll();
            } else {
              this.sharedService.add({
                severity: "error",
                summary: "Error",
                detail: res.message,
              });
              this.displayScroll();
            }
          },
          (error) => {
            this.loading = false;
          }
        );
    }
  }
  handleChange(event) {
    if (event.index == 0) {
      this.status = 1;
      this.getProjects(this.status);
    } else if (event.index == 1) {
      this.status = 0;
      this.getProjects(this.status);
    }
  }
  showDialogToAdd() {
    this.projectType = "Add";
    //this.hiddenScroll();
  }
  onDeptAddCloseClick(event) {
    if (event.type == "add") {
      this.getProjects();
    } else if (event.type == "edit") {
      this.getProjects();
    }
    this.projectType = "";
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }

  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }

  save() {
    this.displayScroll();
  }

  deactivate(rowData: Project) {
    this.projectService
      .deactivateProject(rowData.projectDetailsId)
      .subscribe((res) => {
        if (res.statusCode == 200) {
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.displayDialog = false;
          this.getProjects(this.status);
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
  }
  activate(rowData) {
    this.projectService
      .activateProject(rowData.projectDetailsId)
      .subscribe((res) => {
        if (res.statusCode == 200) {
          this.displayDialog = false;
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.getProjects(this.status);
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
  }
  onAction(rowData) {
    this.project = rowData;
  }
  onProjectAssignSelect(data: Project) {
    this.getProjectDetails(data);
    this.selectedProjectAssignMent = new projectAssignment();
    this.selectedEmployee = 0;
    this.hiddenScroll();
    this.projectType = "assignProject";
  }

  onRowSelect(data: Project) {
    this.loading = true;
    this.projectService.getProject(data.projectDetailsId).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode == 200)
          this.project = res.responsePayload as Project;
      },
      (error) => {
        this.loading = false;
      }
    );
    this.projectType = "Edit";
    // this.editemployeeDisplay = "block";
    // this.newCar = false;
    // this.car = this.cloneCar(data);
    // this.displayDialog = true;
    this.hiddenScroll();
  }
  getProjectDetails(data: Project) {
    this.loading = true;
    this.projectService.getProject(data.projectDetailsId).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode == 200)
          this.project = res.responsePayload as Project;
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  onRowSelectView(data: Project) {
    this.getProjectDetails(data);
    this.projectType = "View";
    this.viewProjectDisplay = "block";
    this.hiddenScroll();
  }
  modalClose() {
    this.editProjectDisplay = "none";
    this.displayScroll();
  }

  closeAddmodal() {
    this.addProjectDisplay = "none";
    this.displayScroll();
  }
  closeViewmodal() {
    this.viewProjectDisplay = "none";
    this.displayScroll();
  }
}
