import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { I9Service } from "../i9.services";
import { AuthService } from "@app/core/service/auth.service";
import { SharedService } from "@app/shared/shared.service";

@Component({
  selector: "app-upload-i9-document",
  templateUrl: "./upload-i9-document.component.html",
  styleUrls: ["./upload-i9-document.component.css"],
})
export class UploadI9DocumentComponent implements OnInit {
  uploadDisplay: string = "none";
  clientErrorMsg: any;
  errLabel: string;
  @Output() onClose = new EventEmitter<any>();
  documentTitle = new FormControl("");
  fileToUpload: any;
  employeeDetailsId: any;
  constructor(
    private i9Services: I9Service,
    private authService: AuthService,
    private sharedService: SharedService
  ) {}

  ngOnInit(): void {
    this.showDialogToAdd();
    this.employeeDetailsId = new FormControl(
      this.authService.currentUser.employeeDetailsId
    );
  }
  showDialogToAdd() {
    this.uploadDisplay = "block";
    this.hiddenScroll();
  }
  closeAddmodal() {
    this.uploadDisplay = "none";
    this.displayScroll();
    this.onClose.emit({ type: "cancel" });
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  onFileChange(event) {
    if (event.target.files.length > 0) {
      let file = event.target.files[0];
      this.fileToUpload = new FormControl(file);
    }
  }
  save() {
    const formModel = this.prepareSave();
    this.i9Services.uploadI9Doc(formModel).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          this.closeAddmodal();
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error!",
            detail: res.message,
          });
        }
      },
      (error) => {
        this.errLabel = error;
      }
    );
  }
  private prepareSave(): any {
    let input = new FormData();
    input.append("documentTitle", this.documentTitle.value);
    input.append("fileToUpload", this.fileToUpload.value);
    input.append("employeeDetailsId", this.employeeDetailsId.value);
    return input;
  }
}
