import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { W4Service } from "../w4.service";
import { SharedService } from "@app/shared/shared.service";
import { FormGroup, FormControl } from "@angular/forms";

@Component({
  selector: "app-upload-w4-document",
  templateUrl: "./upload-w4-document.component.html",
  styleUrls: ["./upload-w4-document.component.css"],
})
export class UploadW4DocumentComponent implements OnInit {
  errLabel: string;
  public uploadDisplay: string = "none";
  clientErrorMsg: any;
  form: FormGroup;
  @Input() userId: any;
  @Output() onClose = new EventEmitter<any>();
  documentTitle: string;
  fileToUpload: any;
  employeeDetailsId: any;

  constructor(
    private w4Services: W4Service,
    private sharedService: SharedService
  ) {}
  ngOnInit(): void {
    this.showDialogToAdd();
    this.employeeDetailsId = new FormControl(this.userId);
  }

  save() {
    const formModel = this.prepareSave();
    this.w4Services.uploadW4Doc(formModel).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.closeAddmodal();
        } else {
          this.errLabel = res.message;
        }
      },
      (error) => {
        this.errLabel = error;
      }
    );
  }
  showDialogToAdd() {
    this.uploadDisplay = "block";
    this.hiddenScroll();
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  closeAddmodal() {
    this.uploadDisplay = "none";
    this.displayScroll();
    this.onClose.emit({ type: "cancel" });
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  onFileChange(event) {
    if (event.target.files.length > 0) {
      let file = event.target.files[0];
      this.fileToUpload = new FormControl(file);
    }
  }
  private prepareSave(): any {
    let input = new FormData();
    input.append("documentTitle", this.documentTitle);
    input.append("fileToUpload", this.fileToUpload.value);
    input.append("employeeDetailsId", this.employeeDetailsId.value);
    return input;
  }
}
